<template>
    <div class="container">
        <div class="border" id="border">
            <img src="../assets/avatar_white.png" class="avatar">
            <div class="summary">
                <!-- 文章相关数据的统计和展示 -->
                <div class="summary_item">
                    <div> {{ name.post_cnt }} </div>
                    <Button :id="post_cnt" :click="goTo" :data="'/posts'">{{ post_cnt }}</Button>
                </div>
                <hr style="height: 60px;">
                <div class="summary_item">
                    <div> {{ name.tag_cnt }} </div>
                    <Button :id="tag_cnt" :click="goTo" :data="'/tags'">{{ tag_cnt }}</Button>
                </div>
            </div>
        </div>

        <div class="pages" id="pages">
            <div v-for="item in pages" :key="item.id">
                <Button :id="item.id" :click="goTo" :data="item.path" :width="button_width">{{ item.name }} </Button>
            </div>
        </div>
    </div>
</template>

<script setup>
import Button from '@/components/ButtonCom.vue'
</script>

<script>
import common from '@/views/CommonConfig.vue'
export default {
    data() {
        return {
            tag_cnt: common.tagCnt,
            post_cnt: common.postCnt,
            pages: [
                {
                    id: 'home',
                    name: '主页',
                    path: '/',
                },
                {
                    id: 'about',
                    name: '关于我',
                    path: '/about'
                },
                {
                    id: 'tags',
                    name: '标签',
                    path: '/tags'
                }
            ],
            name: {},
            button_width: common.width4,
        }
    },
    components: [Button],
    methods: {
        goTo(path) {
            this.$router.push(path);
        },
    },
    mounted() {
        if (common.getLang() === 'CN') {
            this.name = {
                post_cnt: '发布数量',
                tag_cnt: '标签数量',
                about: '关于我'
            }
        } else {
            this.name = {
                post_cnt: 'Post',
                tag_cnt: 'Tags',
                about: 'about',
            }
        }

        for (let index = 0; index < this.pages.length; index++) {
            let item = document.getElementById(this.pages[index].id)
            item.style.width = common.width4 + 'px'
        }
        let border = document.getElementById('border')
        border.style.width = common.width4 + 'px'
        border.style.marginLeft = common.width4 + 'px';
        border.style.marginTop = '50px';

        let pages = document.getElementById('pages')
        pages.style.width = common.width4 + 'px'
        pages.style.marginLeft = common.width4 + 'px';
        pages.style.marginTop = '20px';
    }
}
</script>

