<template>
    <button class="button" :id="id" @mouseover="activeCursor" @mouseleave="nonActiveCursor" @click="buttonClick">
        <slot />
    </button>
</template>
<script>
export default {
    data() {
        return {

        }
    },
    props: {
        id: {
            type: Number,
            required: true,
        },
        width: Number,
        height: Number,
        click: Function,
        data: [Array, Number, String, Object],
    },
    mounted() {
        let item = document.getElementById(this.id);
        if (this.width > 0) {
            item.style.width = this.width + 'px';
        }
        if (this.height > 0) {
            item.style.height = this.height + 'px';
        }
    },
    methods: {
        activeCursor() {
            let cursor = document.getElementById('cursor');
            cursor.classList.add('active');
        },
        nonActiveCursor() {
            let cursor = document.getElementById('cursor');
            cursor.classList.remove('active')
        },
        buttonClick() {
            if (this.click) {
                this.nonActiveCursor();
                this.click(this.data);
            }
        }
    },
}
</script>

<style scoped>
.button {
    border: 0px;
    font-size: 20px;
    background-color: white;
    padding: 10px;
    border-radius: 13px;

    &:hover {
        background-color: #f7f7f7;
    }
}
</style>