</template>

<script>
export default {
  data() {
    return {}
  },
  methods: {
  },
  mounted() {
    const cursor = document.getElementById("cursor");
    window.addEventListener("mousemove", (e) => {
      let x = e.pageX - 5;
      let y = e.pageY - 5;
      cursor.style.transform = `translate(${x}px, ${y}px)`;
    })
    console.log(this.$route.path);
  },
  components: {
  }
}
</script>

