import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  mode: 'hash',
  // base: './',
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeView
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('@/views/AboutView.vue')
    },
    {
      path: '/diary',
      name: 'diary',
      children: [
        {
          path: '2023',
          name: '2023',
          children: [
            {
              path: '8',
              name: '8',
              children: [
                {
                  path: '21',
                  name: '21',
                  component: () => import('@/views/blogs/2023-8-21.vue')
                },
                {
                  path: '24',
                  name: '24',
                  component: () => import('@/views/blogs/2023-8-24.vue')
                },
              ]
            },
          ]
        },

      ]
    }
  ],
})

export default router
